import { Link } from 'react-router-dom';
import { Button, IconButton, List, ListItem, ListItemIcon, ListItemText } from "@material-ui/core";
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import Otsikko from "./Otsikko";
import { format } from 'date-fns'

function Saalislista(props) {

   

    return (

        <>
            <Otsikko tyyli="pieni">Saalislista</Otsikko>

            <Button
                component={Link}
                to="/uusi"
                variant="contained"
                color="primary"
                fullWidth
                size="large"
            >
                Lisää uusi saalis
            </Button>

            <List>
                {props.saaliit.map((saalis, idx) => { 
                return (
                        <ListItem key={idx}>
                            <ListItemText 
                                primary={saalis.laji}
                                secondary={`
                                    ${format(saalis.deadline, "d.M.y HH:mm")}
                                    ${saalis.paikka}
                                    ${saalis.paino}${"g"}
                                    ${saalis.uistin}
                                `}
                            />
        
                            <ListItemIcon>
                                <IconButton
                                    component={Link}
                                    to={`/poista/${saalis.id}`}
                                >
                                    <DeleteIcon/>
                                </IconButton>
                            </ListItemIcon>
                            <ListItemIcon>
                                <IconButton
                                    component={Link}
                                    to={`/muokkaa/${saalis.id}`}
                                >
                                    <EditIcon/>
                                </IconButton>
                            </ListItemIcon>
                            
                        </ListItem>
                        )
                })}
            </List>

      </>

    )

}

export default Saalislista;