import { Button, Typography } from '@material-ui/core';
import { Link, useHistory, useParams } from 'react-router-dom';
import Otsikko from './Otsikko';

function PoistaSaalis(props) {

    const history = useHistory();
    const { id } = useParams();

    const poistaSaalis = () => {

        let saaliitApu = props.saaliit.filter((saalis) => {
            return (saalis.id !== id);            
        });        

        props.setSaaliit([...saaliitApu]);

        history.push("/");
    
    }

    return (
        <>
            <Otsikko tyyli="pieni">Poista bongaus</Otsikko>

            <Typography>Haluatko varmasti poistaa bongauksen "{props.saaliit.filter((saalis) => {
                                                                return (saalis.id === id);
                                                            })[0].laji}"?</Typography>

            <Button
                variant="contained"
                color="primary"
                fullWidth
                size="large"
                onClick={poistaSaalis}
                style={{marginTop : 10}}
            >
                Poista bongaus
            </Button>

            <Button
                component={Link}
                to="/"
                variant="contained"
                color="default"
                fullWidth
                size="large"
                style={{marginTop : 10}}
            >
                Peruuta
            </Button>

        </>
    )

}

export default PoistaSaalis;