import { Button, TextField } from '@material-ui/core';
import { useState } from 'react';
import { Link, useHistory } from 'react-router-dom';
import Otsikko from './Otsikko';
import { MuiPickersUtilsProvider, DateTimePicker } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import { fi } from 'date-fns/locale';
import { v4 as uuid } from 'uuid';
import React from 'react';


function UusiSaalis(props) {

    const [laji, setUusiLaji] =useState();
    const [paikka, setPaikka] = useState();
    const [pvmAika, setPvmAika] = useState(new Date());
    const [paino, setPaino] = useState();
    const [uistin, setUistin] = useState();
    const history = useHistory();
 
    const lisaaSaalis = () => {

        let saaliitApu = {
                            id : uuid(),
                            laji: laji,
                            deadline : pvmAika.getTime(),
                            paikka : paikka,
                            paino: paino,
                            uistin: uistin,
                            suoritettu : false,
                         }
    
        props.setSaaliit([...props.saaliit, saaliitApu]);

        history.push("/");
    
    }


    return (
        <MuiPickersUtilsProvider utils={DateFnsUtils} locale={fi}>
            <Otsikko tyyli="pieni">Lisää uusi saalis</Otsikko>


            <TextField 
                variant="outlined"
                fullWidth
                placeholder="Laji"
                style={{marginBottom : 10}}
                onChange={(e) => {
                                    setUusiLaji(e.target.value);                                    
                                }}
            />

            <DateTimePicker 
                label="Saalistettu aika"
                inputVariant="outlined"
                fullWidth
                format="d.M.y HH:mm"
                ampm={false}
                cancelLabel="Peruuta"
                value={pvmAika}
                onChange={setPvmAika}
                style={{marginBottom : 10}}
            />

            <TextField 
                variant="outlined"
                fullWidth
                placeholder="Saanti paikka"
                style={{marginBottom : 10}}
                onChange={(e) => {
                                    setPaikka(e.target.value);                                    
                                }}
            />

            <TextField 
                variant="outlined"
                fullWidth
                placeholder="Saaliin koko g"
                style={{marginBottom : 10}}
                onChange={(e) => {
                                    setPaino(e.target.value);                                    
                                }}
            />

            <TextField 
                variant="outlined"
                fullWidth
                placeholder="Käytetty uistin"
                value={uistin}
                style={{marginBottom : 10}}
                onChange={(e) => {
                                    setUistin(e.target.value);                                    
                                }}
            />

            <Button
                variant="contained"
                color="primary"
                fullWidth
                size="large"
                onClick={lisaaSaalis}
                style={{marginTop : 10}}
            >
                Tallenna
            </Button>

            <Button
                component={Link}
                to="/"
                variant="contained"
                color="default"
                fullWidth
                size="large"
                style={{marginTop : 10}}
            >
                Peruuta
            </Button>

        </MuiPickersUtilsProvider>
    )

}

export default UusiSaalis;