import { useState, useEffect } from 'react'; 
import { BrowserRouter as Router, Route } from 'react-router-dom';
import Lista from './components/Lista';
import Uusi from './components/Uusi';
import Otsikko from './components/Otsikko';
import Poista from './components/Poista';


function App() {

  const [saaliit, setSaaliit] = useState([]);

  const tallennaTiedot = () => {

    localStorage.setItem("lista", JSON.stringify(saaliit));

  }

  const avaaLista = () => {

      if (localStorage.getItem("lista")) {

        setSaaliit(JSON.parse(localStorage.getItem("lista")));

      } else {

        setSaaliit([]);

      }    

  }

  useEffect(() => {

    avaaLista();

  }, []);

  useEffect(() => {

    tallennaTiedot();
     // eslint-disable-next-line
  }, [saaliit]);
 

  return (
    <Router>

      <Otsikko>Saaliit</Otsikko>    

      <Route path="/" exact>
        <Lista setSaaliit={setSaaliit} saaliit={saaliit} tallennaTiedot={tallennaTiedot}/>
      </Route>

      <Route path="/uusi">
        <Uusi setSaaliit={setSaaliit} saaliit={saaliit}/>
      </Route>

      <Route path="/poista/:id">
        <Poista setSaaliit={setSaaliit} saaliit={saaliit}/>
      </Route>

    </Router>
  );
}

export default App;